import { Actions } from '@app/actions/shared';
import {
  isMessageOfType,
  type SubscriptionExpiringModalClickedMessage,
  SubscriptionExpiringModalClickedStatus,
  InitSubscriptionExpiringScriptReadyMessage,
  SubscriptionExpiringModalStatus,
} from '@app/messages/shared';
import { browser_api } from '@app/browser_api';
import crossIcon from '../assets/icons/cross.svg';

const SUBSCRIPTION_EXPIRING_MODAL_CONTAINER_ID =
  'subscription-expiring-message-container';

const initSubscriptionExpiringOnYoutubePageListener = async (
  message: unknown,
): Promise<void> => {
  if (isMessageOfType(message, Actions.ShowSubscriptionExpiringModal)) {
    showSubscriptionExpiringModalOnPage(message.payload.status);
  }
};

const hideSubscriptionExpiringOnYoutubePageListener = async (
  message: unknown,
): Promise<void> => {
  if (isMessageOfType(message, Actions.HideSubscriptionExpiringModal)) {
    closeSubscriptionExpiringModal();
  }
};

function checkFullscreen(element: HTMLElement | null): void {
  const isFullscreen = document.fullscreenElement;

  if (!element) {
    return;
  }

  if (isFullscreen) {
    element.style.display = 'none';
  } else {
    element.style.display = 'block';
  }
}

function fullscreenChangeHandler(): void {
  const container = document.getElementById(
    SUBSCRIPTION_EXPIRING_MODAL_CONTAINER_ID,
  );
  checkFullscreen(container);
}

export async function initSubscriptionExpiringOnYoutubePage(): Promise<void> {
  try {
    document.addEventListener('fullscreenchange', fullscreenChangeHandler);

    if (
      !browser_api.runtime.onMessage.hasListener(
        initSubscriptionExpiringOnYoutubePageListener,
      )
    ) {
      browser_api.runtime.onMessage.addListener(
        initSubscriptionExpiringOnYoutubePageListener,
      );
    }

    if (
      !browser_api.runtime.onMessage.hasListener(
        hideSubscriptionExpiringOnYoutubePageListener,
      )
    ) {
      browser_api.runtime.onMessage.addListener(
        hideSubscriptionExpiringOnYoutubePageListener,
      );
    }

    await browser_api.runtime.sendMessage<InitSubscriptionExpiringScriptReadyMessage>(
      {
        action: Actions.InitSubscriptionExpiringScriptReady,
      },
    );
    console.warn(
      'Script "subscriptionExpiringOnYoutubePage" initialized successfully',
    );
  } catch (error) {
    console.error(error);
  }
}

function showSubscriptionExpiringModalOnPage(
  status: SubscriptionExpiringModalStatus,
): void {
  closeSubscriptionExpiringModal();
  try {
    const currentSubscriptionExpiringModalElement =
      createSubscriptionExpiringModalElement(status);

    document.body.appendChild(currentSubscriptionExpiringModalElement);
    checkFullscreen(currentSubscriptionExpiringModalElement);

    // Add animation
    setTimeout(() => {
      currentSubscriptionExpiringModalElement?.classList.add(
        'subscription-expiring-modal-visible',
      );
    }, 100);
  } catch (error) {
    console.error('Error on showing subscription expiring modal', error);
  }
}

function createSubscriptionExpiringModalElement(
  status: SubscriptionExpiringModalStatus,
): HTMLElement {
  const container = document.createElement('div');
  container.id = SUBSCRIPTION_EXPIRING_MODAL_CONTAINER_ID;

  const header = document.createElement('div');
  const headerText = document.createElement('h3');
  headerText.textContent =
    status === 'expiring' ? '  ' : '  ';
  header.classList.add('subscription-expiring-modal__header');
  const crossImg = document.createElement('img');
  crossImg.src = crossIcon;
  crossImg.alt = '';
  const closeButton = document.createElement('button');
  closeButton.appendChild(crossImg);
  closeButton.classList.add('subscription-expiring-modal__close-button');
  closeButton.addEventListener('click', () => {
    handleSubscriptionExpiringModalClick('close');
  });

  header.appendChild(headerText);
  header.appendChild(closeButton);

  const description = document.createElement('p');
  description.textContent =
    '        ';
  description.classList.add('subscription-expiring-modal__description');

  const buttons = document.createElement('div');
  const buySubscriptionButton = document.createElement('button');
  buySubscriptionButton.textContent = ' ';
  buySubscriptionButton.classList.add('accent');
  buySubscriptionButton.addEventListener('click', () => {
    handleSubscriptionExpiringModalClick('buy-subscription');
  });
  const goToFreeButton = document.createElement('button');
  goToFreeButton.textContent = '   ';
  goToFreeButton.addEventListener('click', () => {
    handleSubscriptionExpiringModalClick('go-to-free');
  });

  buttons.appendChild(buySubscriptionButton);
  buttons.appendChild(goToFreeButton);
  buttons.classList.add('subscription-expiring-modal__buttons');

  container.appendChild(header);
  container.appendChild(description);
  container.appendChild(buttons);
  container.classList.add('subscription-expiring-modal');

  addSubscriptionExpiringModalStyles();

  return container;
}

async function handleSubscriptionExpiringModalClick(
  status: SubscriptionExpiringModalClickedStatus,
): Promise<void> {
  try {
    await browser_api.runtime.sendMessage<SubscriptionExpiringModalClickedMessage>(
      {
        action: Actions.SubscriptionExpiringModalClicked,
        payload: { status },
      },
    );
  } catch (error) {
    console.warn('Error handling subscription expiring modal click:', error);
  }
}

function closeSubscriptionExpiringModal(): void {
  const existingSubscriptionExpiringModal = document.getElementById(
    SUBSCRIPTION_EXPIRING_MODAL_CONTAINER_ID,
  );
  if (existingSubscriptionExpiringModal) {
    existingSubscriptionExpiringModal.remove();
  }
}

function addSubscriptionExpiringModalStyles(): void {
  const existingStyles = document.getElementById(
    'subscription-expiring-modal-styles',
  );
  if (existingStyles) {
    return;
  }

  const style = document.createElement('style');
  style.id = 'subscription-expiring-modal-styles';
  style.textContent = `
    .subscription-expiring-modal {
      position: fixed;
      right: 22px;
      bottom: 25px;
      width: 480px;
      max-width: calc(100% - 80px);
      z-index: 999999;
      padding: 16px;
      background-color: #1e1e1e;
      border-radius: 20px;
      border: 1px solid #232323;
      opacity: 0;
      transform: translateX(100%);
      transition: all 0.3s ease;
    }

    .subscription-expiring-modal.subscription-expiring-modal-visible {
      opacity: 1;
      transform: translateX(0);
    }

    .subscription-expiring-modal button {
      background: none;
      border: none;
      padding: 0;
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: all 0.3s ease;
    }

    .subscription-expiring-modal button:hover {
      opacity: 0.75;
    }

    .subscription-expiring-modal__header {
      width: 100%;
      display: flex;
      justify-content: space-between;
      gap: 8px;
      margin-bottom: 4px;
    }

    .subscription-expiring-modal__header h3 {
      margin: 0;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      font-style: normal;
      font-weight: 700;
      line-height: 140%;
      letter-spacing: -0.42px;
      color: #fff;
    }

    .subscription-expiring-modal__close-button {
      width: 16px;
      height: 16px;
      cursor: pointer;
    }

    .subscription-expiring-modal__close-button img {
      width: 100%;
      height: 100%;
    }

    .subscription-expiring-modal__description {
      width: 410px;
      max-width: 100%;
      margin: 0 0 8px;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      font-style: normal;
      font-weight: 600;
      line-height: 140%;
      letter-spacing: -0.42px;
      color: rgba(255, 255, 255, 0.6);
    }

    .subscription-expiring-modal__buttons {
      display: flex;
      gap: 14px;
      align-items: center;
      flex-wrap: wrap;
    }

    .subscription-expiring-modal__buttons button {
      padding: 10px;
      border-radius: 12px;
      border: 1px solid rgba(255, 255, 255, 0.4);
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 100%;
      letter-spacing: -0.42px;
      color: rgba(255, 255, 255, 0.6);
    }

    .subscription-expiring-modal__buttons button.accent {
      color: #fff;
      border-color: #fff;
    }
  `;

  document.head.appendChild(style);
}
