import { Actions } from '@app/actions/shared';
import {
  isMessageOfType,
  type InitGiftAdvertisingScriptReadyMessage,
  type GiftClickedMessage,
} from '@app/messages/shared';
import { browser_api } from '@app/browser_api';

import cakeIcon from '../assets/icons/cake.png';
import yellowGiftIcon from '../assets/icons/yellow_gift.png';
import redPlayIcon from '../assets/icons/red_play_icon.png';
import redTvIcon from '../assets/icons/red_tv.png';

const GIFT_IMAGES = [cakeIcon, yellowGiftIcon, redPlayIcon, redTvIcon];

const GIFT_CONTAINER_ID = 'youtube-gift-container';
const GIFT_HIDDEN_CLASS = 'youtube-gift-hidden';

let currentGiftElement: HTMLElement | null = null;

const initGiftsOnYoutubePageListener = async (
  message: unknown,
): Promise<void> => {
  if (isMessageOfType(message, Actions.InitGiftAdvertisingScript)) {
    showGiftOnPage();
  }
};

const hideGiftOnYoutubePageListener = async (
  message: unknown,
): Promise<void> => {
  if (isMessageOfType(message, Actions.HideGift)) {
    hideGiftFor24Hours();
  }
};

export async function initGiftsOnYoutubePage(): Promise<void> {
  try {
    if (
      !browser_api.runtime.onMessage.hasListener(initGiftsOnYoutubePageListener)
    ) {
      browser_api.runtime.onMessage.addListener(initGiftsOnYoutubePageListener);
    }

    if (
      !browser_api.runtime.onMessage.hasListener(hideGiftOnYoutubePageListener)
    ) {
      browser_api.runtime.onMessage.addListener(hideGiftOnYoutubePageListener);
    }

    await browser_api.runtime.sendMessage<InitGiftAdvertisingScriptReadyMessage>(
      {
        action: Actions.InitGiftAdvertisingScriptReady,
      },
    );
  } catch (error) {
    console.error(error);
  }
}

function showGiftOnPage(): void {
  hideCurrentGift();

  const randomGiftUrl =
    GIFT_IMAGES[Math.floor(Math.random() * GIFT_IMAGES.length)];

  currentGiftElement = createGiftElement(randomGiftUrl);

  positionGiftRandomly(currentGiftElement);

  document.documentElement.appendChild(currentGiftElement);

  // Add animation
  setTimeout(() => {
    currentGiftElement?.classList.add('youtube-gift-visible');
  }, 100);
}

function createGiftElement(imageUrl: string): HTMLElement {
  const giftContainer = document.createElement('div');
  giftContainer.id = GIFT_CONTAINER_ID;
  giftContainer.className = 'youtube-gift';

  const giftImage = document.createElement('img');
  giftImage.src = imageUrl;
  giftImage.alt = '';
  giftImage.className = 'youtube-gift-image';

  giftContainer.appendChild(giftImage);

  giftContainer.addEventListener('click', handleGiftClick);

  addGiftStyles();

  return giftContainer;
}

function positionGiftRandomly(element: HTMLElement): void {
  const viewportWidth = window.innerWidth;
  const viewportHeight = window.innerHeight;

  const giftSize = 150;
  const margin = 20;

  const maxX = viewportWidth - giftSize - margin;
  const maxY = viewportHeight - giftSize - margin;

  const randomX = Math.random() * Math.max(0, maxX) + margin;
  const randomY = Math.random() * Math.max(0, maxY) + margin;

  const rotationAngles = [-30, -20, -10, 0, 10, 20, 30];
  const randomRotation =
    rotationAngles[Math.floor(Math.random() * rotationAngles.length)];

  element.style.left = `${randomX}px`;
  element.style.top = `${randomY}px`;
  element.style.rotate = `${randomRotation}deg`;
}

async function handleGiftClick(): Promise<void> {
  try {
    await browser_api.runtime.sendMessage<GiftClickedMessage>({
      action: Actions.GiftClicked,
    });

    hideGiftFor24Hours();
  } catch (error) {
    console.warn('Error handling gift click:', error);
  }
}

function hideGiftFor24Hours(): void {
  if (currentGiftElement) {
    currentGiftElement.classList.add(GIFT_HIDDEN_CLASS);
    currentGiftElement.style.display = 'none';
  }
}

function hideCurrentGift(): void {
  const existingGift = document.getElementById(GIFT_CONTAINER_ID);
  if (existingGift) {
    existingGift.remove();
  }
  currentGiftElement = null;
}

function addGiftStyles(): void {
  const existingStyles = document.getElementById('youtube-gift-styles');
  if (existingStyles) {
    return;
  }

  const style = document.createElement('style');
  style.id = 'youtube-gift-styles';
  style.textContent = `
    .youtube-gift {
      cursor: pointer !important;
      position: fixed;
      width: 170px;
      height: 170px;
      z-index: 9999999;
      opacity: 0;
      transform: scale(0.5);
      transition: all 0.3s ease;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .youtube-gift::before {
      content: '';
      position: absolute;
      top: 15px;
      left: 15px;
      right: 15px;
      bottom: 15px;
      background: linear-gradient(45deg, #7EC7FF, #C97EFF);
      border-radius: inherit;
      filter: blur(8px);
      z-index: -1;
      opacity: 0.3;
    }

    .youtube-gift.youtube-gift-visible {
      opacity: 1;
      transform: scale(1);
    }

    .youtube-gift-image {
      width: 150px;
      height: 150px;
      object-fit: contain;
      animation: giftFloat 3s ease-in-out infinite;
      border-radius: 50%;
      z-index: 1;
      position: relative;
    }

    .youtube-gift:hover {
      transform: scale(1.1);
    }

    .youtube-gift-hidden {
      display: none !important;
    }

    @keyframes giftFloat {
      0%, 100% {
        transform: scale(1);
      }
      50% {
        transform: scale(1.1);
      }
    }
  `;

  document.head.appendChild(style);
}
